		&__cont {
			align-items: flex-start;
			justify-content: flex-start;
			padding: 18px 16px; }

		// &__timer, &__players, &__counter
		/// width: 48%

		&__players {
			// position: fixed
			top: 12px;
			right: 90px;
			margin-bottom: 1rem; }

		&__counter {
			width: 100%;
			margin-bottom: 1rem; } }

	.question {

		&__answers {
			bottom: 0;
			position: fixed;
			right: 0;
			width: 45%;
			top: 0;
			background: $second-color;
			padding: 18px;
			// align-items: center
			display: flex;
			flex-direction: column;
			justify-content: center;
			left: auto; }

		&__text {
			font-size: rem(20);
			margin-bottom: 0; } }

	.answers {
		margin-left: 0;
		// justify-content: space-between

		&__one {
			width: 100%;
			margin-left: 0;

			&:not(:last-child) {
				margin-bottom: 15px; } } }

	.answer {
		height: auto;
		min-height: 0;
		padding: 8px 10px; } }

@media screen and (max-width: 670px) and (orientation: landscape) {

	@supports (display: grid) {
		.profile {
			padding: 20px; } } }

@media screen and (max-width: 400px) {

	.awards {
		margin: 0;
		flex-wrap: wrap;
		margin-bottom: -30px;

		&__one {
			width: 50%;
			margin: 0;
			margin-bottom: 30px;
			justify-content: center; } } }

@media screen and (max-width: 370px) {
	html {
		font-size: 93%; }

	form[action="/usergame/?login=yes"] {

		.auth {
			&-form__footer {

				.auth-form__footer-submit, .auth-form__footer-forget {
					width: auto; }

				.auth-form__forget-link {
					padding: 14px 16px 12px;
					text-transform: none;
					font-size: rem(13); } } } }


	.arrow-btn {
		padding: 14px 16px 12px;
		text-transform: none;
		font-size: rem(13); }

	.g-recaptcha {
		display: flex;
		justify-content: center; }

	.arrow-btn {
		width: 100%;
		text-align: left;
		display: block; }

	.auth-form {

		&__footer {
			flex-direction: column;
			align-items: flex-start;

			&-submit, &-forget {
				width: 100%;

				&:not(:first-child) {
					margin: 15px 0 0; } } }

		&__soc {
			flex-direction: column;
			align-items: flex-start;

			.soc {
				margin: 15px 0 0; } } }

	.game-filed {

		&__cont {
			padding: 15px; }

		&__counter {
			margin-bottom: 1rem; } }

	.question {

		&__text {
			font-size: rem(24); } }

	.edit__input-cont {
		margin-bottom: 10px;

		&--double .edit__input-cont {
			margin-bottom: 0 !important;
			width: calc(50% - 4px); } }

	.edit-profile {

		.arrow-btn {
			padding: 12px 20px 10px !important;
			background: $main-color !important;
			text-align: center; } } }
