			&-hover {
				background: darken($second-color, 7%);
				color: #fff; }

			&-active {
				background: $second-color;
				color: #fff; } }

		td {

			&.active {
				background: $second-color; } } } }

#ui-datepicker-div {
	box-shadow: $shadow;
	background: #3848aa;
	border-radius: 15px;
	border: none;
	overflow: hidden; }

.ui-widget {

	&-header {

		.ui {

			&-icon {
				@include size(16px);
				border-radius: 50%;
				background: $main-color;
				position: relative;

				&:after {
					content: "";
					position: absolute;
					top: calc(50% - 3px);
					left: calc(50% - 3px);
					@include triangle(6px 5px, #fff, left); }

				&-circle-triangle-e {
